<template> 
  <el-card class="form-container" shadow="never">
    <el-form :model="homeAdvertise"
             :rules="rules"
             ref="homeAdvertiseFrom"
             label-width="150px"
             size="small">
      <el-form-item label="广告名称：" prop="name">
        <el-input v-model="homeAdvertise.bannerName" class="input-width"></el-input>
      </el-form-item>
      <el-form-item label="广告位置：">
        <el-select v-model="homeAdvertise.bannerType">
          <el-option
            v-for="type in typeOptions"
            :key="type.value"
            :label="type.label"
            :value="type.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="开始时间：" prop="startTime">
        <el-date-picker
          type="datetime"
          placeholder="选择日期"
          v-model="homeAdvertise.startDate"></el-date-picker>
      </el-form-item>
      <el-form-item label="到期时间：" prop="endTime">
        <el-date-picker
          type="datetime"
          placeholder="选择日期"
          v-model="homeAdvertise.endDate"></el-date-picker>
      </el-form-item>
      <el-form-item label="上线/下线：">
        <el-radio-group v-model="homeAdvertise.status">
          <el-radio :label="2">下线</el-radio>
          <el-radio :label="1">上线</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="广告图片：">
        <!--<single-upload v-model="homeAdvertise.imageUrl"></single-upload>-->
        <el-upload
          action="none"
          :show-file-list="false"
          :on-success ="uploadProductUrl"
          :on-change="checkType"
          class="avatar-uploader"

          accept=".jpg,.jpeg,.png,.gif">
          <img v-if="homeAdvertise.imageUrl" :src="homeAdvertise.imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"/>
        </el-upload>
      </el-form-item>
      <el-form-item label="排序：">
        <el-input v-model="homeAdvertise.sortIndex" class="input-width"></el-input>
      </el-form-item>
      <el-form-item label="广告链接：" prop="url">
        <el-input v-model="homeAdvertise.jumpUrl" class="input-width"></el-input>
      </el-form-item>
      <el-form-item label="广告备注：">
        <el-input
          class="input-width"
          type="textarea"
          :rows="5"
          placeholder="请输入内容"
          v-model="homeAdvertise.description">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('homeAdvertiseFrom')">提交</el-button>
        <el-button v-if="!isEdit" @click="resetForm('homeAdvertiseFrom')">重置</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
  import SingleUpload from '@/components/Upload/singleUpload'
  import {createHomeAdvertise, getHomeAdvertise, updateHomeAdvertise} from '@/api/homeAdvertise'
  import {  uploadPath } from '@/api/storage'
  const defaultTypeOptions = [
    {
      label: 'PC首页轮播',
      value: 1
    },
    {
      label: 'APP首页轮播',
      value: 2
    }
  ];
  const defaultHomeAdvertise = {
    bannerName: null,
     jumpUrl: null,
    bannerType: 1,
    description: null,
     sortIndex: 0,
    imageUrl: null,
    startDate: null,
    endDate: null,
    status: 0,
  };
  export default {
    name: 'HomeAdvertiseDetail',
    components:{SingleUpload},
    props: {
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        homeAdvertise: null,
        uploadPath,
        rules: {
          bannerName: [
            {required: true, message: '请输入广告名称', trigger: 'blur'},
            {min: 2, max: 140, message: '长度在 2 到 140 个字符', trigger: 'blur'}
          ],
          jumpUrl: [
            {required: true, message: '请输入广告链接', trigger: 'blur'}
          ],
          startDate: [
            {required: true, message: '请选择开始时间', trigger: 'blur'}
          ],
          endDate: [
            {required: true, message: '请选择到期时间', trigger: 'blur'}
          ],
          imageUrl: [
            {required: true, message: '请选择广告图片', trigger: 'blur'}
          ]
        },
        typeOptions: Object.assign({}, defaultTypeOptions)
      }
    },
    created(){
      console.log(uploadPath);
      console.log(this.uploadPath)
      if (this.isEdit) {

        console.log('bannerId is' + this.$route.query.bannerId);

        getHomeAdvertise(this.$route.query.bannerId).then(response => {
          this.homeAdvertise = response.data;
        });
      }else{
        this.homeAdvertise = Object.assign({},defaultHomeAdvertise);
      }
    },
    methods: {
      handlePictureCardPreview(file){
   console.log(file);
      },
        uploadProductUrl: function(response) {
          console.log(response);
          console.log('uploadSuccess')
          this.homeAdvertise.imageUrl = response.data
        },
        checkType(file){
          console.log(file);
          this.homeAdvertise.file=file.raw;
          if (window.createObjectURL!=undefined) { // basic
          this.homeAdvertise.imageUrl = window.createObjectURL(file.raw) ;
        } else if (window.URL!=undefined) { // mozilla(firefox)
          this.homeAdvertise.imageUrl = window.URL.createObjectURL(file.raw) ;
        } else if (window.webkitURL!=undefined) { // webkit or chrome
        this.homeAdvertise.imageUrl = window.webkitURL.createObjectURL(file.raw) ;
        }
        },
      onSubmit(formName) {
         let formData=new FormData();
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('是否提交数据', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              if (this.isEdit) {
                console.log(this.homeAdvertise);
                formData.append('file',this.homeAdvertise.file);
                formData.append('jumpUrl',this.homeAdvertise.jumpUrl);
                formData.append('bannerName',this.homeAdvertise.bannerName);
                formData.append('bannerType',this.homeAdvertise.bannerType);
                formData.append('description',this.homeAdvertise.description);
                formData.append('sortIndex',this.homeAdvertise.sortIndex);
                formData.append('startDate',this.homeAdvertise.startDate);
                formData.append('endDate',this.homeAdvertise.endDate);
                formData.append('activated',this.homeAdvertise.activated);
                console.log(formData)
                updateHomeAdvertise(this.$route.query.bannerId,formData).then(response => {
                  this.$refs[formName].resetFields();
                  this.$message({
                    message: '修改成功',
                    type: 'success',
                    duration:1000
                  });
                  this.$router.back();
                });
              } else {
                createHomeAdvertise(this.homeAdvertise).then(response => {
                  this.$refs[formName].resetFields();
                  this.homeAdvertise = Object.assign({},defaultHomeAdvertise);
                  this.$message({
                    message: '提交成功',
                    type: 'success',
                    duration:1000
                  });
                });
              }
            });

          } else {
            this.$message({
              message: '验证失败',
              type: 'error',
              duration:1000
            });
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.homeAdvertise = Object.assign({},defaultHomeAdvertise);
      }
    }
  }
</script>
<style scoped>
  .input-width {
    width: 70%;
  }
</style>





